export const Code2 = `switch (type) {
  case LOAD_ALL_ARTICLES:
      return articlesState
          .set("entities", arrToMap(response, ArticleRecord))
          .set("loading", false);

  case ADD_ARTICLE:
      return articlesState.setIn(["entities", randomId], {
          ...payload.article,
          id: randomId
      });
  default:
      return articlesState;
}
`;

